import {Fragment, useEffect, useState} from 'react';
import MetaData from '../layout/MetaData';
import {clearAuthError, login} from '../../actions/userActions';
import { useDispatch, useSelector } from 'react-redux';
import { toast } from 'react-toastify';
import { Link, useLocation, useNavigate } from 'react-router-dom';

export default function Login(){

    const [email, setEmail] = useState("");   //To store email from user...
    const [password, setPassword] = useState("");   //To store password from user...

    const dispatch = useDispatch();

    const navigate = useNavigate();

    const location = useLocation();

    const { loading, error, isAuthenticated } = useSelector(state => state.authState);

    const redirect = location.search?'/'+location.search.split('=')[1] : '/';  //For cart shipping process redirecting...

    const submitHandler = (e) => {
        e.preventDefault();
        dispatch(login(email, password));   //send as a arguments to userAsctions using dispatch...
    }

    useEffect(() => {
        if(isAuthenticated) {
            navigate(redirect);  //If user logged in it redirect to shipping page/ if not redirect to login page..
        }

        if(error) {
            toast(error, {
                position : "bottom-center",
                type : 'error',
                onOpen: () => {  //It will change the error value to null once user logged in...
                    dispatch(clearAuthError);
                }
            })
        }
        return
    }, [error, isAuthenticated, dispatch, navigate]);  //whenever login happens the page redirect to home by giving login inside dependencies, if not it load only once....

    return (
        <Fragment>
            <MetaData title={`Login`}/>
            <div className="row wrapper"> 
                <div className="col-10 col-lg-5">
                <form onSubmit={submitHandler} className="shadow-lg">
                    <h1 className="mb-3">Login</h1>
                    <div className="form-group">
                    <label htmlFor="email_field">Email</label>
                    <input
                        type="email"
                        id="email_field"
                        className="form-control"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}  //It stores the email value...
                    />
                    </div>
        
                    <div className="form-group">
                    <label htmlFor="password_field">Password</label>
                    <input
                        type="password"
                        id="password_field"
                        className="form-control"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}  //It stores the password value..
                    />
                    </div>

                    <Link to="/password/forgot" className="float-right mb-4">Forgot Password?</Link>
        
                    <button
                    id="login_button"
                    type="submit"
                    className="btn btn-block py-3"
                    disabled = {loading}
                    >
                    LOGIN
                    </button>

                    <Link to= '/register' className="float-right mt-3">New User?</Link>
                </form>
                </div>
        </div>
        </Fragment>
    )
}